.hero {
	padding: 30px 0 110px;
	background: #2F8E59;
	background-image: linear-gradient(102deg, #2F8E59 19%, #86E8AB 98%);
	&__inner {
		margin: 0 auto;
		max-width: 1300px;
		font-size: 0; }
	&__total,
	&__sm-info,
	&__main-address {
		display: inline-block;
		vertical-align: top;
		padding: 24px 16px;
		width: 33.33%;
		@include sm {
			padding-left: 20px;
			padding-right: 20px; } }
	&__total,
	&__sm-info {
		@include md {
			width: 50%; }
		@include sm {
			width: 100%; } }
	&__main-address {
		@include md {
			width: 100%; } }
	&__graphic {
		margin-bottom: 68px;
		@include xs {
			margin-bottom: 30px; } }
	&__items {
		margin: 0 -14px; }
	&__item {
		position: relative;
		display: inline-block;
		vertical-align: top;
		padding: 14px;
		width: 33.33%;
		color: $white;
		text-align: center;
		@include xs {
			padding: 14px 0; }
		&:not(:last-child) {
			&:before {
				content: '';
				position: absolute;
				top: 27px;
				right: 0;
				bottom: 31px;
				border-left: 1px solid $white;
				opacity: 0.22; } } }
	&__value {
		margin-bottom: 21px;
		opacity: 0.9;
		@include f(32px, 600, 50px);
		letter-spacing: -0.67px;
		@include xs {
			@include f(24px, null, 1.4); } }
	&__label {
		opacity: 0.75;
		@include f(16px, 400, 29px);
		@include xs {
			@include f(11px, null, 1.4); } } }
