.success {
	text-align: center;
	padding: 96px 0 90px;
	@include sm {
		padding: 50px 0 30px; }
	&__img {
		margin: 0 auto 40px; } }

.success-list {
	font-size: 0;
	margin: 0 -15px;
	padding: 90px 0 100px;
	@include sm {
		padding: 50px 0 30px;
		margin: 0 auto;
		max-width: 250px; }
	&__item {
		display: inline-block;
		vertical-align: top;
		width: 33.33%;
		padding: 0 15px;
		@include sm {
			padding: 0;
			width: 100%;
			margin-bottom: 30px;
			text-align: center; } }
	&__img {
		margin-bottom: 16px;
		@include sm {
			margin: 0 auto 15px; } }
	&__title {
		color: $white;
		margin-bottom: 17px;
		@include f(18px, null, 24px); }
	&__text {
		@include f(15px, null, 24px);
		color: #ceceda;
		a {
			color: #ffa27b;
			text-decoration: none;
			&:hover {
				text-decoration: underline; } } } }
